body {
  --outer-padding: 0px;
  --border-width: 0px;
  --separator-width: 2px;
  --inner-padding: 8px;
  --input-box-margin: 0px;
}
@media (min-width: 800px) {
  body {
    --outer-padding: 8px;
    --border-width: 2px;
    --input-box-margin: 8px;
  }
}

/* */

.light {
  --bg-color: hsl(0, 0%, 100%, 1);
  --main-color: hsl(0, 0%, 0%, 0.8);
  --accent-color: hsl(0, 0%, 0%, 0.8);
  --input-color: hsl(0, 0%, 0%, 0.8);
}

.dim {
  --bg-color: hsl(0, 0%, 20%, 1);
  --main-color: hsl(0, 0%, 100%, 0.8);
  --accent-color: hsl(0, 0%, 100%, 0.8);
  --input-color: hsl(0, 0%, 100%, 0.8);
}

.dark {
  --bg-color: hsl(0, 0%, 0%, 1);
  --main-color: hsl(0, 0%, 100%, 1);
  --accent-color: hsl(0, 0%, 100%, 1);
  --input-color: hsl(0, 0%, 100%, 1);
}

