html, body {
  background: #000;
  height: 100%;
  margin: 0;
  font-family: Open Sans, sans-serif;
  font-size: 18px;
  line-height: 27px;
}

#root {
  height: 100%;
}

body * {
  scrollbar-color: var(--main-color) var(--bg-color);
  scrollbar-width: thin;
}

body ::-webkit-scrollbar {
  width: var(--inner-padding);
}

body ::-webkit-scrollbar-thumb {
  background-color: var(--main-color);
  border: calc(.5 * var(--inner-padding)) solid var(--bg-color);
  border-left-width: 0;
}

:focus-visible {
  outline: calc(.5 * var(--separator-width)) solid var(--main-color);
  outline-offset: calc(1px + var(--separator-width));
}

::selection {
  color: var(--bg-color);
  background: var(--main-color);
}

a, summary {
  cursor: pointer;
  border-bottom: var(--separator-width) solid currentColor;
  color: inherit;
  text-decoration: none;
  display: inline;
}

a:focus, a:hover, summary:focus, summary:hover {
  opacity: .8;
}

hr {
  border: 0;
  border-top: var(--separator-width) solid var(--main-color);
  height: 0;
}

ul {
  list-style: square;
}

button {
  border: var(--separator-width) solid var(--main-color);
  background-color: var(--bg-color);
  color: var(--main-color);
  padding: var(--inner-padding) calc(4 * var(--inner-padding));
  font-family: inherit;
  font-size: inherit;
  cursor: pointer;
}

input::placeholder {
  color: var(--main-color);
  opacity: .8;
}

input, select {
  box-sizing: border-box;
  padding: calc(.5 * var(--inner-padding)) var(--inner-padding);
  color: var(--main-color);
  background-color: var(--bg-color);
  border: var(--separator-width) solid var(--main-color);
  font: inherit;
}

select {
  cursor: pointer;
  -webkit-appearance: none;
  appearance: none;
  background-image: repeating-linear-gradient(315deg, var(--bg-color), var(--bg-color) var(--inner-padding), var(--main-color) var(--inner-padding), var(--main-color) calc(var(--inner-padding)  + var(--separator-width)), var(--bg-color) calc(var(--inner-padding)  + var(--separator-width)), var(--bg-color) 100%);
  border-radius: 0;
  width: 250px;
}

label {
  width: 100%;
  max-width: 400px;
  display: inline-block;
}

label input {
  width: 100%;
}

input[type="file"] {
  text-indent: -100%;
  cursor: pointer;
  font-size: 0;
  position: relative;
}

body {
  --outer-padding: 0px;
  --border-width: 0px;
  --separator-width: 2px;
  --inner-padding: 8px;
  --input-box-margin: 0px;
}

@media (min-width: 800px) {
  body {
    --outer-padding: 8px;
    --border-width: 2px;
    --input-box-margin: 8px;
  }
}

